import React, { useState } from 'react'
import { Container, GroupItem, WechatCode, TelNo } from './style'

export default function LeftSead() {
  let [telhide, setTelHide] = useState(true)
  let [codehide, setCodeHide] = useState(true)

  const handleScrollTop = () => {
    window.scrollTo({
      left: 0,
      top: 0,
      behavior: 'smooth',
    })
  }

  return (
    <Container>
      <GroupItem 
        imgUrl='https://eyongtech.oss-cn-hangzhou.aliyuncs.com/letron/telPhone.png'
        onMouseEnter={() => setTelHide(false)}
        onMouseLeave={() => setTelHide(true)}
      >
        <div className='img' />
        <p className='tit'>电话咨询</p>
      </GroupItem>
      <GroupItem 
        imgUrl='https://eyongtech.oss-cn-hangzhou.aliyuncs.com/letron/wechat.png'
        onMouseEnter={() => setCodeHide(false)}
        onMouseLeave={() => setCodeHide(true)}
      >
        <div className='img' />
        <p className='tit'>微信咨询</p>
      </GroupItem>
      <GroupItem 
        imgUrl='https://eyongtech.oss-cn-hangzhou.aliyuncs.com/letron/toTop.png'
        onClick={handleScrollTop}
      >
        <div className='img' />
        <p className='tit'>回到顶部</p>
      </GroupItem>
      {!telhide &&
        <TelNo>
          <p>咨询电话:</p>
          <p>15888843932</p>
        </TelNo>}
      {!codehide && <WechatCode />}
    </Container>
  )
}